#empty-question-contaiter{
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

#empty-question-contaiter{
    display: flex;
    flex-direction: column;
}

#empty-question-contaiter h2{
    flex: none;
    order: 0;
    flex-grow: 0;
    margin: 4px 0px;
    font-weight: 500;
    font-size: 20px;
    line-height: 28px;
    text-align: center;
    color: #FFFFFF;
}

#empty-question-contaiter span{
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    display: flex;
    align-items: center;
    text-align: center;
    color: #71767A;
    flex: none;
    order: 1;
    flex-grow: 0;
    margin: 4px 0px;
}