#reply-question-container{
    position: absolute;
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    background-color: #131414;
}

#reply-question-container button{
    margin: 16px;
    height: 48px;
    background: #65CC5C;
    border-radius: 100px;
    border: none;
}   

#reply-close-container{
    height: 56px;
    background: #131414;
    border-radius: 12px 12px 0px 0px;
    display: flex;
    align-items: center;
    padding: 0px 16px;
}

#reply-question-container h2{
    font-weight: 500;
    font-size: 20px;
    line-height: 28px;
    color: #FFFFFF;
    margin: 0px;
    padding-bottom: 15px;
}

#reply-text-container{
    padding: 0px 16px;
    display: flex;
    flex: 1;
    flex-direction: column;
}

#reply-text-container input{
    height: 22px;
    border: none;
    color: white;
    caret-color: white;
    background: #181919;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    border-radius: 16px;
    padding: 10px 0px 10px 10px;
}
#reply-text-container input:focus{
    outline: none;
}

#reply-text-container span{
    color: red;
    margin-bottom: 4px;
}

