#bottom-menu-container{
    display:flex;
    height: 82px;
    background: #1D1E1F;
    justify-content: space-around;
}

.icon-container, .icon-container-select{
    margin-top: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.icon-select-link{
    pointer-events: none;
    user-select: none;
    cursor: default;
}

.icon-container > svg > path{
    fill: #71767A;
}

.icon-container-select > svg > path {
    fill: #65CC5C;
}