#question-menu-container{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    font-weight: 500;
    font-size: 20px;
    line-height: 28px;
    height: 80px;
    align-items: center;
}

.question-button-menu{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.question-button-menu h2{
    user-select: none;
    font-weight: 500;
    font-size: 20px;
    line-height: 28px;
    text-align: center;
    text-decoration: none;
    margin: 0 0 4px 0;
    /* color: #71767A; */
}