.me-qestion-container{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 16px 0px;
    height: 126px;
    background: #131414;
    border-radius: 16px;
    margin: 16px 0px;
    align-self: stretch;
    flex-grow: 0;
}

.me-qestion-text{
    display: flex;
    flex-direction: column;
    margin: 16px 0px;
    padding: 0px 16px;
}

.me-qestion-text p {
    font-weight: 500;
    font-size: 16px;
    line-height: 22px;
    color: #FFFFFF;
    margin: 2px 0px;
}

.me-qestion-text > span{
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    color: #71767A;
    margin: 2px 0px;
}

.me-question-action{
    display: flex;
    justify-content: space-between;
    align-self: stretch;
    flex-grow: 0;
    margin: 0 16px;
}

.me-question-action button{
    border: none;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    padding: 0px 16px;
    width: 106px;
    height: 36px;
    background: #65CC5C;
    border-radius: 100px;
    font-weight: 500;
    font-size: 16px;
    line-height: 22px;
}